@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

/* Initial Resets */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", "Poppins";
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  width: 100vw;
  overflow-x: hidden;
}

:root {
  --primary: #5e3bee;
  --heading-color: #282938;
  --bg-shade: #e7f8ff;
  --github: #e62872;
  --darkblue: #1c1e53;
  --black: #000;
  --white: #fff;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

/* Utility Classes */
.sectionPadding {
  padding: 5rem 10rem;
}

.btn {
  padding: 1.5rem 2rem;
  outline: none;
  border: none;
  font-weight: 600;
  font-size: 1.8rem;
  background-color: transparent;
  border-radius: 0.5rem;
  transition: 0.3s all ease-in-out;
}

.btn-primary:hover,
.btn-primary-outline {
  border: 1px solid var(--primary);
  color: var(--primary);
  background-color: transparent;
}

.btn-primary,
.btn-primary-outline:hover {
  border: 1px solid var(--primary);
  background-color: var(--primary);
  color: var(--white);
  cursor: pointer;
}

.heading {
  font-size: 6rem;
  border-bottom: 5px solid var(--primary);
  width: 100%;
  padding-bottom: 0.5rem;
  color: var(--heading-color);
  display: inline;
}

/* Media Queries */
@media (max-width: 1040px) {
  .sectionPadding {
    padding: 5rem 8rem;
  }
}

@media (max-width: 768px) {
  .sectionPadding {
    padding: 5rem 6rem;
  }
  .heading {
    font-size: 4rem;
  }
}

@media (max-width: 576px) {
  .sectionPadding {
    padding: 5rem 4rem;
  }
}

@media (max-width: 480px) {
  .sectionPadding {
    padding: 5rem 2rem !important;
  }

  .heading {
    font-size: 3rem;
  }
}
